import Vue from 'vue'

import { Dialog } from 'vant'

Vue.use(Dialog)

export default {

	//
	dialogTip(title, isClear, o, property) {

		//title: 
		//isClear: 
		//o: 
		//property: o

		Dialog.alert({
			title,
		}).then(() => {

			if (typeof isClear === 'boolean' && isClear) {
				o[property] = '';
			}

		})
	}

}